/* Mobile */
@media screen and (max-width: 768px) {
    .title {
        position: absolute;
        right: 50%;
        transform: translateX(50%);
        font-family: 'Karantina';
        font-size: 16vw;
        color: #50565c;
        width: 100%;
        top: 20vh;
        text-align: center;
    }

    .div-list {
        position: absolute;
        display: flex;
        flex-direction: column;
        margin-top: 49vh;
        right: 50%;
        width: 80vw;
        transform: translateX(50%);
    }

    .photo-list-item {
        align-items: center;
        font-size: 2.5vh;
        padding-bottom: 1vh;
    }

    .photo-list-content {
        display: flex;
        flex-grow: 1;
        align-items: center;
    }

    .photo-list-image-homepage {
        width: 17vw;
        height: auto;
    }

    .photo-list-title-homepage {
        flex-grow: 1;
        text-align: right;
        font-size: 3vh;
        color: #50565c;
        font-weight: bolder;
        padding-right: 5%;
    }

    .photo-list-item:last-child {
        border-bottom: none;
    }

    .dot-line {
        position: relative;
        right: 50%;
        transform: translateX(50%);
        margin-bottom: 2vh;
        padding-bottom: -2vh;
    }

    .mashov-home {
        font-family: 'Heebo', sans-serif;
        font-size: 4vw;
        color: #50565c;
        text-align: center;
        position: absolute;
        top: 123vh;
        right: 50%;
        transform: translateX(50%);
        width: 67vw;
    }

    .mashovText {
        margin-bottom: 4vh;
    }

    #linkHome {
        color: #50565c;
    }
}

/* Tablet */
@media screen and (min-width: 769px) and (max-width: 1024px) {}

